import * as React from 'react';

import CollageDisplay from './CollageDisplay';
import FileGetter from './FileGetter';

interface CollageImage {
  name: string;
  data?: string;
  width?: number;
  height?: number;
  error: boolean;
}

interface ImageListProps {
  images: CollageImage[];
}

const ImageList = (props: ImageListProps) => (
  <div>
    <h3>Images</h3>
    <div
      style={{
        overflowY: 'auto',
        width: 300,
        height: 400,
        border: '1px solid #888',
        backgroundColor: '#f7f7f7',
        borderRadius: '1em',
        display: 'grid',
        gridTemplateColumns: '1fr 1fr 1fr'
      }}
    >
      {props.images.map((el, i) => (
        <ImageListItem key={i} {...el} />
      ))}
    </div>
  </div>
);

const ImageListItem = (props: CollageImage) => {
  if (props.error) {
    return <div style={{ color: 'red' }}>{props.name}</div>;
  }
  return (
    <div style={{ display: 'grid' }}>
      <div>
        <img src={props.data} width={64} height={64} />
      </div>
      <div>{props.name}</div>
    </div>
  );
};

interface CollageState {
  images: CollageImage[];
  width: number;
  height: number;
  rows: number;
  columns: number;
}

export default class Collage extends React.Component<{}, CollageState> {
  constructor(props: {}) {
    super(props);
    this.state = {
      images: [],
      width: 512,
      height: 512,
      rows: 4,
      columns: 4
    };

    this.onImageUpload = this.onImageUpload.bind(this);
    this.onImageError = this.onImageError.bind(this);
    this.onImageSwap = this.onImageSwap.bind(this);
    this.onImageRemove = this.onImageRemove.bind(this);
  }

  onImageUpload(name: string, data: string) {
    const images = [...this.state.images];
    images.push({
      name: name,
      data: data,
      error: false
    });
    this.setState({ images: images });
  }

  onImageError(name: string) {
    const images = [...this.state.images];
    images.push({
      name: name,
      error: true
    });
    this.setState({ images: images });
  }

  onImageSwap(from: number, to: number) {
    if (Math.floor(from) != from || Math.floor(to) != to) {
      return;
    } else if (
      from < 0 ||
      from >= this.state.images.length ||
      to < 0 ||
      to >= this.state.images.length
    ) {
      return;
    }

    const images = [...this.state.images];
    const temp = images[from];
    images[from] = images[to];
    images[to] = temp;
    this.setState({ images: images });
  }

  onImageRemove(index: number) {
    if (
      Math.floor(index) != index ||
      index < 0 ||
      index >= this.state.images.length
    ) {
      return;
    }

    const images = [...this.state.images];
    images.splice(index, 1);
    this.setState({ images: images });
  }

  render() {
    return (
      <div>
        <h1>Collage Designer</h1>
        <div style={{ display: 'grid', gridTemplateColumns: '380px auto' }}>
          <div>
            <ImageList images={this.state.images} />
            <div>
              <h3>Add</h3>
              <FileGetter
                onImageUpload={this.onImageUpload}
                onImageError={this.onImageError}
              />
            </div>
            <div style={{ display: 'grid', gridTemplateColumns: '1fr 3fr' }}>
              <h3 style={{ gridColumnStart: 1, gridColumnEnd: 3 }}>
                Properties
              </h3>
              <div>Width</div>
              <div>
                <input
                  type="text"
                  value={this.state.width}
                  size={2}
                  onChange={e =>
                    this.setState({ width: parseInt(e.target.value) })
                  }
                />
              </div>
              <div>Height</div>
              <div>
                <input
                  type="text"
                  value={this.state.height}
                  size={2}
                  onChange={e =>
                    this.setState({ height: parseInt(e.target.value) })
                  }
                />
              </div>
              <div>Rows</div>
              <div>
                <input
                  type="text"
                  value={this.state.rows}
                  size={2}
                  onChange={e =>
                    this.setState({ rows: parseInt(e.target.value) })
                  }
                />
              </div>
              <div>Columns</div>
              <div>
                <input
                  type="text"
                  value={this.state.columns}
                  size={2}
                  onChange={e =>
                    this.setState({ columns: parseInt(e.target.value) })
                  }
                />
              </div>
            </div>
          </div>
          <div>
            <h3>Collage</h3>
            <div
              style={{
                backgroundColor: '#eee',
                borderRadius: '1em',
                display: 'inline-block'
              }}
            >
              <CollageDisplay
                {...this.state}
                onImageSwap={this.onImageSwap}
                onImageRemove={this.onImageRemove}
              />
            </div>
          </div>
        </div>
      </div>
    );
  }
}
