import * as React from 'react';

interface FileGetterProps {
  onImageUpload?: (name: string, dataUrl: string) => void;
  onImageError?: (name: string) => void;
}

interface FileGetterState {
  pending: File[];
  working: boolean;
  current?: File;
}

export default class FileGetter extends React.Component<
  FileGetterProps,
  FileGetterState
> {
  constructor(props: FileGetterProps) {
    super(props);
    this.state = { pending: [], working: false, current: null };

    this.onFileChange = this.onFileChange.bind(this);
  }

  onFileChange(event: React.ChangeEvent<HTMLInputElement>) {
    const pending = [...this.state.pending];
    for (let i = 0; i < event.target.files.length; i++) {
      pending.push(event.target.files[i]);
    }

    this.setState({ pending: pending }, () => {
      if (!this.state.working) {
        this.setState({ working: true }, () => this.processPending());
      }
    });
  }

  processPending() {
    if (this.state.pending.length == 0) {
      this.setState({ working: false });
      return;
    }
    const pending = [...this.state.pending];
    const curr = pending.shift();

    const reader = new FileReader();
    reader.onload = e => {
      if (this.props.onImageUpload) {
        this.props.onImageUpload(curr.name, e.target.result as string);
      }
      this.processPending();
    };
    reader.onerror = e => {
      if (this.props.onImageError) {
        this.props.onImageError(curr.name);
      }
      this.processPending();
    };
    reader.readAsDataURL(curr);
    this.setState({ pending: pending, current: curr });
  }

  render() {
    return (
      <div>
        <input
          type="file"
          multiple={true}
          accept="image/*"
          onChange={this.onFileChange}
        />
        <div>{this.state.working ? 'Uploading...' : ''}</div>
      </div>
    );
  }
}
