import * as React from 'react';

interface DisplayImage {
  name: string;
  data?: string;
  width?: number;
  height?: number;
  error: boolean;
}

interface CollageDisplayProps {
  images: DisplayImage[];
  width: number;
  height: number;
  rows: number;
  columns: number;
  onImageSwap?: (from: number, to: number) => void;
  onImageRemove?: (index: number) => void;
}

interface CollageDisplayState {
  swap: number;
  file?: string;
  name?: string;
  rendering: boolean;
}

export default class CollageDisplay extends React.Component<
  CollageDisplayProps,
  CollageDisplayState
> {
  canvas: HTMLCanvasElement = null;

  constructor(props: CollageDisplayProps) {
    super(props);
    this.state = { swap: -1, rendering: false };

    this.onImageClick = this.onImageClick.bind(this);
    this.onImageRemove = this.onImageRemove.bind(this);
    this.renderImage = this.renderImage.bind(this);
    this.canvas = document.createElement('canvas');
  }

  onImageClick(img: number) {
    if (this.state.swap == -1) {
      this.setState({ swap: img });
      return;
    }
    if (this.props.onImageSwap) {
      this.props.onImageSwap(this.state.swap, img);
    }
    this.setState({ swap: -1 });
  }

  onImageRemove() {
    if (this.state.swap == -1) {
      return;
    }
    if (this.props.onImageRemove) {
      this.props.onImageRemove(this.state.swap);
    }
    this.setState({ swap: -1 });
  }

  render() {
    const colWidth = this.props.width / this.props.columns;
    const cols = Array(this.props.columns)
      .fill(`${colWidth}px`)
      .join(' ');
    const rowHeight = this.props.height / this.props.rows;
    const rows = Array(this.props.rows)
      .fill(`${rowHeight}px`)
      .join(' ');

    const count = this.props.rows * this.props.columns;
    this.canvas.width = this.props.width;
    this.canvas.height = this.props.height;
    const ctx = this.canvas.getContext('2d');
    ctx.fillRect(0, 0, this.props.width, this.props.height);

    const imgs = [];
    for (let i = 0; i < count && i < this.props.images.length; i++) {
      const image = this.props.images[i];

      if (image.data && this.state.rendering) {
        const x = (i % this.props.columns) * colWidth;
        const y = Math.floor(i / this.props.columns) * rowHeight;
        const img = new Image(colWidth, rowHeight);
        img.onload = (e: Event) => {
          console.log(`Render ${image.name}`);
          ctx.drawImage(
            img,
            0,
            0,
            img.naturalWidth,
            img.naturalHeight,
            x,
            y,
            colWidth,
            rowHeight
          );
        };
        img.src = image.data;
      }

      const style: React.CSSProperties = {};
      if (i == this.state.swap) {
        style.border = '2px solid red';
        style.width = colWidth - 4;
        style.height = rowHeight - 4;
      } else {
        style.width = colWidth;
        style.height = rowHeight;
      }
      imgs.push(
        <div key={i}>
          <img
            style={style}
            src={image.data}
            onClick={e => this.onImageClick(i)}
          />
        </div>
      );
    }

    return (
      <div style={{ margin: '1em' }}>
        <div
          style={{
            margin: '0.25em',
            width: this.props.width,
            height: this.props.height,
            display: 'grid',
            backgroundColor: '#FFF',
            gridTemplateColumns: cols,
            gridTemplateRows: rows
          }}
        >
          {imgs}
        </div>
        <button
          style={{ width: 75, height: 25, margin: '0.25em' }}
          disabled={!this.state.rendering}
          onClick={this.renderImage}
        >
          Save
        </button>
        <button
          style={{
            backgroundColor: this.state.rendering ? 'red' : 'silver',
            width: 75,
            height: 25,
            margin: '0.25em'
          }}
          onClick={e => this.setState({ rendering: !this.state.rendering })}
        >
          Rendering
        </button>
        <button
          style={{ width: 150, height: 25, margin: '0.25em' }}
          disabled={this.state.swap < 0}
          onClick={this.onImageRemove}
        >
          Remove{' '}
          {this.state.swap >= 0 ? this.props.images[this.state.swap].name : ''}
        </button>
        <div style={{ display: this.state.file ? 'inline-block' : 'none' }}>
          Download{' '}
          <a
            href={this.state.file}
            download={this.state.name}
            style={{ display: this.state.file ? 'inline' : 'none' }}
          >
            {this.state.name}
          </a>
        </div>
      </div>
    );
  }

  renderImage() {
    const now = new Date().toLocaleString();
    this.setState({
      file: this.canvas.toDataURL('image/jpeg'),
      name: `collage-${now}.jpg`
    });
  }
}
